@font-face {
  font-family: 'fontello';
  src: url('@{fontsPrefix}plone-fontello.eot');
  src: url('@{fontsPrefix}plone-fontello.eot#iefix') format('embedded-opentype'),
       url('@{fontsPrefix}plone-fontello.woff') format('woff'),
       url('@{fontsPrefix}plone-fontello.ttf') format('truetype'),
       url('@{fontsPrefix}plone-fontello.svg#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
// Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. 
// Note, that will break hinting! In other OS-es font will be not as sharp as it could be 

